import React from 'react'
import { Link } from "gatsby"
import esprapages_logo from '../images/esprapages_logo.svg';

class Header extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      hasScrolled: false
    }
  }
  componentDidMount() {
    window.addEventListener('scroll', this.handleScroll)
}
handleScroll = (event) => {
  const scrollTop = window.pageYOffset

  if (scrollTop > 50) {
    this.setState({ hasScrolled: true })
  } else {
    this.setState({ hasScrolled: false })
  }
}
  render() {
    return (

      <div className={this.state.hasScrolled ? 'Header HeaderScrolled' : 'Header'}>




        {/* <div className="availability">
          <h6>I am currently not available but feel free to get in touch !
          <Link className="fat_underline" to="#contactForm" onClick="track('Click-contact-project-topbar-index','Click-contact-project-topbar-index')">  contact me</Link></h6>
        </div> */}

        <nav className="sidebar-nav">
          <Link className="menu__item" to="/"><img src={esprapages_logo} alt="test" /></Link>
                <Link className="menu__item" to="#books"><span className="menu__item-name">books</span></Link>
                <Link className="menu__item" to="/artworks/"><span className="menu__item-name">artwork</span></Link>
                <Link className="menu__item" to="/music/"><span className="menu__item-name">music</span></Link>
                <Link className="menu__item" to="/about/"><span className="menu__item-name">about</span></Link>
        </nav>




      </div>

    )
  }
}

export default Header
